import React, { Fragment } from "react";
import smartContranctImg from "../../styles/assets/images/smartcontractdev/smartcontract1.svg";
import smartContranctImg2 from "../../styles/assets/images/smartcontractdev/smartcontract2.jpg";
import smartContranctImg3 from "../../styles/assets/images/smartcontractdev/smartcontract3.jpg";
import { Carousel } from "react-bootstrap";
import { Link } from "gatsby";

const DevelopmentServices = () => {
  return (
    <Fragment>
      <div className="business-wrap my-5 pt-5">
        <div className="container-fluid">
          <div className="business-need p-5 row">
            <div className="col-12">
              <div className="mx-5">
                <h3 className="mb-2 fw-bold">
                  SMART CONTRACT DEVELOPMENT SERVICES
                </h3>
                <p className="text-dark fw-semibold fs-3 mt-4">
                  Unlock the future of trust and automation with Justtry
                  technologies. Our Smart Contract Development Services
                  revolutionize the way business agreements are executed. We
                  harness &nbsp;
                  <Link
                    className="inter_link"
                    target="_blank"
                    rel="noopener noreferrer"
                    href="/enterprise-blockchain-page"
                  >
                    blockchain technology
                  </Link>{" "}
                  to create secure, tamper-proof, and self-executing contracts
                  tailored to your needs.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container smcarousel">
        <div className="d-flex align-items-center row">
          <div className="row">
            <div className="col-md-12">
              <Carousel>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="content">
                        <p className="fs-3 fw-semibold">
                          Our seasoned team of blockchain developers is
                          proficient in crafting smart contracts on Ethereum,
                          Binance Smart Chain, and more. Whether it's &nbsp;
                          <Link
                            href="/decentralize-exchange"
                            target="_blank"
                            rel="noopener noreferrer"
                            className="inter_link"
                          >
                            decentralized{" "}
                          </Link>{" "}
                          applications (DApps), tokenization, or supply chain
                          solutions, we turn your ideas into code.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={smartContranctImg}
                        alt="smartcontranct-1-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="content">
                        <div className="content">
                          <p className="fs-3 fw-semibold">
                            What sets us apart is our commitment to transparency
                            and security. We ensure your smart contracts are
                            bug-free, audited, and aligned with industry
                            standards. Trust us to streamline your processes,
                            reduce disputes, and increase efficiency.
                          </p>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={smartContranctImg2}
                        alt="smartcontranct-2-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="content">
                        <p className="fs-3 fw-semibold">
                          Smart contracts offer the promise of efficiency,
                          transparency, and cost savings across industries. They
                          eliminate the need for intermediaries, reduce
                          disputes, and enhance trust among stakeholders.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={smartContranctImg3}
                        alt="smartcontranct-3-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
              </Carousel>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default DevelopmentServices;
